






















































import mpvueCityPicker from '../../components/mpvue-citypicker/mpvueCityPicker.vue';
import common from '../../common/common.js';
import {
	mapState
} from 'vuex';
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			user: {
				agent_use: '',
				bank_name: '',
				bank_card: '',
				user_name: '',
				bank_address: ''
			},
			index: -1,
			tiqu_money: 0,
			picker: ['喵喵喵', '汪汪汪', '哼唧哼唧'],
			region: '',
			checkbox: [{
				value: 0,
				name: '10元',
				money: '10',
				checked: false,
				hot: false,
			}, {
				value: 1,
				name: '20元',
				money: '20',
				checked: false,
				hot: false,
			}, {
				value: 2,
				name: '30元',
				money: '30',
				checked: false,
				hot: true,
			}, {
				value: 3,
				name: '60元',
				money: '60',
				checked: false,
				hot: true,
			}, {
				value: 4,
				name: '80元',
				money: '80',
				checked: false,
				hot: false,
			}, {
				value: 5,
				name: '100元',
				money: '100',
				checked: false,
				hot: false,
			}],
			type_checkbox: [{
				value: 'A',
				checked: true
			}, {
				value: 'B',
				checked: true
			}, {
				value: 'C',
				checked: false
			}],
			radio:0
		}
	},
	onLoad(option) {
		let that = this;
		uni.request({
			url: common.userInfoUrl, //仅为示例，并非真实接口地址。
			data: {
				is_mobile: 1,
				user_id: that.userInfo.id
			},
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				console.log(res.data.data.bank_address)
				if (res.data.status == 1) {
					that.user.bank_address = res.data.data.bank_address;
					that.user.bank_name = res.data.data.bank_name;
					that.user.agent_use = res.data.data.agent_use;
					that.user.user_name = res.data.data.user_name;
					that.picker = res.data.data.user_bank;


				}



			}
		});
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'bi'])
	},
	methods: {
		input(e){
			let items = this.checkbox;
			
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
			
				items[i].checked = false;
			
			
			}
			  this.tiqu_money=e.detail.value
		},
		focus(e){
			let items = this.checkbox;
			
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
			
				items[i].checked = false;
			
			
			}
			
			
			
			
		},
		PickerChange(e) {

			this.index = e.detail.value;
			console.log(this.index)
			let that = this;
			that.user.bank_name = that.userInfo.user_bank[this.index];
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		ChooseCheckbox(e) {
			let items = this.checkbox;
			let values = e.currentTarget.dataset.value;
			for (let i = 0, lenI = items.length; i < lenI; ++i) {

				items[i].checked = false;


			}
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
				if (items[i].value == values) {
					items[i].checked = !items[i].checked;
					console.log(items[i].money)
					this.tiqu_money = items[i].money
					break
				}
			}
		},
		chooseCity() {
			this.$refs.mpvueCityPicker.show()
		},
		//提交
		confirm() {
			let data = this.user;
			let that = this;
			if (!that.radio) {
				this.$api.msg('请选择提现方式');
				return;
			}
					console.log(that.radio)

			if (that.tiqu_money == 0) {
				this.$api.msg('请选择金额');
				return;
			}

			uni.request({
				url: common.frontCurrencyConfirm, //仅为示例，并非真实接口地址。
				data: {
					price: that.tiqu_money,
					is_mobile: 1,
					user_id: that.userInfo.id,
					openid:  that.userInfo.gzh_openid,
					alipay:  that.userInfo.alipay,

					ttype: that.radio
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					if (res.data.status == 1) {

						that.$api.msg(res.data.info);
						setTimeout(() => {
							uni.navigateBack()
						}, 800)
					} else {

						that.$api.msg(res.data.info);

					}



				}
			});

		},
	}
}
